.header {
    display: flex;
    justify-content: space-between;
    padding: 20px 60px 20px 20px;
    gap: 50px;
    margin-bottom: 10px;
    align-items: baseline;
    font-size: 18px;
    box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
      0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
  }
  .headerTitle {
    font-size: 18px;
    color: #3f51b5;
  }
  .headerNav {
    display: flex;
    gap: 5px;
    font-weight: 500;
  }
  .NavLink {
    color: #212121;
  }
  .footer {
    position: fixed;
    left: 0px;
    bottom: 0px;
    width: 100%;
    background-color: #3f51b5;
    color: white;
    border-top: 2px solid rgba(22, 8, 214, 0.2);
  }
  .footerText {
    text-align: center;
    padding: 10px 0;
    margin: 0;
  }
  